.switchBox {
  margin-bottom: 5%;
  height: 100%;
  width: 50%;
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
}

.switch {
  position: relative;

  input {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    z-index: 100;
    cursor: pointer;
  }

  label {
    height: 45px;
    width: 105px;
    background-color: $colorBg;
    border: 2px solid rgba(255, 255, 255, 0.5);
    border-radius: 100px;
    display: block;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.2),
      inset 0 0 5px -2px rgba(0, 0, 0, 0.4);

    .bulb {
      height: 35px;
      width: 35px;
      background-color: #4a426b;
      border-radius: 50%;
      position: relative;
      top: 3px;
      left: 5px;
      display: block;
      -webkit-transition: 600ms;
      transition: 600ms;
      box-shadow: inset 0 0 1px 3px #4a426b, inset 0 0 6px 8px #423963,
        0 20px 30px -10px rgba(0, 0, 0, 0.4);

      .bulb-center {
        position: absolute;
        display: block;
        height: 36px;
        width: 36px;
        background-color: #5a527b;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        -webkit-transition: 600ms;
        transition: 600ms;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        box-shadow: inset 0 0 0 4px #635a84;

        &:after {
          content: "";
          display: block;
          height: 14px;
          width: 14px;
          background-color: #7b7394;
          border-radius: 50%;
          position: absolute;
          -webkit-transition: 600ms;
          transition: 600ms;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
          box-shadow: 0 0 2px 4px #524a73;
        }
      }

      .filament-1,
      .filament-2 {
        position: absolute;
        display: block;
        height: 35px;
        width: 35px;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        overflow: hidden;
        -webkit-transform: translate(-50%, -50%) rotate(-45deg);
        transform: translate(-50%, -50%) rotate(-45deg);
      }

      .filament-1:after,
      .filament-2:after,
      .filament-1:before,
      .filament-2:before {
        content: "";
        display: block;
        height: 6px;
        width: 17px;
        border-radius: 50%;
        border: 2px solid #4a426b;
        position: absolute;
        -webkit-transition: 600ms;
        transition: 600ms;
        top: -4px;
        left: -2px;
        -webkit-transform: rotate(-10deg);
        transform: rotate(-10deg);
      }

      .filament-1:before {
        left: 15px;
        -webkit-transform: rotate(10deg);
        transform: rotate(10deg);
      }

      .filament-2 {
        &:before {
          left: 15px;
          -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
        }

        -webkit-transform: translate(-50%, -50%) rotate(45deg) !important;
        transform: translate(-50%, -50%) rotate(45deg) !important;
      }
    }
  }
}

.reflections {
  height: 100%;
  width: 100%;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  z-index: 90;
  -webkit-perspective: 70px;
  perspective: 70px;

  span {
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background-image: linear-gradient(
      -135deg,
      transparent 10%,
      rgba(255, 255, 255, 0.3)
    );
    position: absolute;
    left: -40px;
    bottom: -45px;

    &:after {
      content: "";
      display: block;
      height: 35px;
      width: 20px;
      position: absolute;
      top: -36px;
      right: -40px;
      border-radius: 50%;
      box-shadow: 4px -2px 0 -3px rgba(255, 255, 255, 0.4);
      -webkit-filter: blur(1px);
      filter: blur(1px);
      -webkit-transform: rotate(-10deg);
      transform: rotate(-10deg);
    }
  }

  &:after {
    content: "";
    display: block;
    height: 80px;
    width: 50px;
    background-image: linear-gradient(
      80deg,
      rgba(255, 255, 255, 0.05) 45%,
      rgba(255, 255, 255, 0.5)
    );
    border-radius: 10% 20% 50% 30%/30% 60% 30% 40%;
    position: absolute;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transform: rotateX(-25deg) rotate(-35deg) skewx(-15deg)
      translate(10px, -20px);
    transform: rotateX(-25deg) rotate(-35deg) skewx(-15deg)
      translate(10px, -20px);
    top: -8px;
    left: -5px;
  }

  &:before {
    content: "";
    display: block;
    position: absolute;
    height: 10px;
    width: 30px;
    background-image: -webkit-gradient(
      linear,
      left top,
      right top,
      from(transparent),
      to(rgba(255, 255, 255, 0.15))
    );
    background-image: linear-gradient(
      to right,
      transparent,
      rgba(255, 255, 255, 0.15)
    );
    bottom: 10px;
    right: 0px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
}

.sparks {
  .spark1 {
    display: block;
    height: 1px;
    width: 1px;
    background-color: #d1b82b;
    position: absolute;
    right: -5px;
    border-radius: 50%;
    bottom: 23px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    opacity: 0;
  }

  .spark2 {
    display: block;
    height: 3px;
    width: 3px;
    background-color: #d1b82b;
    position: absolute;
    right: 20px;
    border-radius: 50%;
    bottom: 80px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    opacity: 0;
  }

  .spark3 {
    display: block;
    height: 3px;
    width: 3px;
    background-color: #d1b82b;
    position: absolute;
    left: 20px;
    border-radius: 50%;
    bottom: 80px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    opacity: 0;
  }

  .spark4 {
    display: block;
    height: 3px;
    width: 3px;
    background-color: #d1b82b;
    position: absolute;
    left: 20px;
    border-radius: 50%;
    bottom: 20px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    opacity: 0;
  }
}

.switch input:checked ~ label .bulb {
  left: 60px;
  background-color: #a7694a;
  box-shadow: inset 0 0 1px 3px #a56758, inset 0 0 6px 8px #6b454f,
    0 20px 30px -10px rgba(0, 0, 0, 0.4), 0 0 30px 50px rgba(253, 184, 67, 0.1);

  > {
    .bulb-center {
      background-color: #feed6b;
      box-shadow: inset 0 0 0 4px #fdec6a, 0 0 12px 10px #bca83c,
        0 0 20px 14px #a1664a;

      &:after {
        background-color: #fef401;
        box-shadow: 0 0 2px 4px #fdb843;
      }
    }

    .filament-1:before,
    .filament-2:before,
    .filament-1:after,
    .filament-2:after {
      border-color: #fef4d5;
    }

    .sparks {
      .spark1 {
        height: 1px;
        width: 1px;
        -webkit-animation: spark1 2s ease-in-out;
        animation: spark1 2s ease-in-out;
        -webkit-animation-delay: 0.4s;
        animation-delay: 0.4s;
      }

      .spark2 {
        height: 1px;
        width: 1px;
        -webkit-animation: spark2 2.4s ease-in-out;
        animation: spark2 2.4s ease-in-out;
        -webkit-animation-delay: 0.4s;
        animation-delay: 0.4s;
      }

      .spark3 {
        height: 1px;
        width: 1px;
        -webkit-animation: spark3 2s ease-in-out;
        animation: spark3 2s ease-in-out;
        -webkit-animation-delay: 0.9s;
        animation-delay: 0.9s;
      }

      .spark4 {
        height: 1px;
        width: 1px;
        -webkit-animation: spark4 1.7s ease-in-out;
        animation: spark4 1.7s ease-in-out;
        -webkit-animation-delay: 0.9s;
        animation-delay: 0.9s;
      }
    }
  }
}

@-webkit-keyframes spark1 {
  0% {
    right: -5px;
    height: 1px;
    width: 1px;
    opacity: 0;
  }

  20% {
    height: 3px;
    width: 3px;
    right: 0px;
    opacity: 1;
  }

  30% {
    right: -5px;
    opacity: 1;
    height: 3px;
    width: 3px;
  }

  70% {
    height: 3px;
    width: 3px;
  }

  100% {
    right: -60px;
    bottom: 40px;
    opacity: 0;
  }
}

@keyframes spark1 {
  0% {
    right: -5px;
    height: 1px;
    width: 1px;
    opacity: 0;
  }

  20% {
    height: 3px;
    width: 3px;
    right: 0px;
    opacity: 1;
  }

  30% {
    right: -5px;
    opacity: 1;
    height: 3px;
    width: 3px;
  }

  70% {
    height: 3px;
    width: 3px;
  }

  100% {
    right: -60px;
    bottom: 40px;
    opacity: 0;
  }
}

@-webkit-keyframes spark2 {
  0% {
    height: 3px;
    width: 3px;
    opacity: 0;
  }

  30% {
    opacity: 1;
  }

  100% {
    right: -20px;
    bottom: 100px;
    opacity: 0;
  }
}

@keyframes spark2 {
