@-webkit-keyframes spark3 {
  0% {
    opacity: 0;
  }

  30% {
    opacity: 1;
    height: 2px;
    width: 2px;
  }

  100% {
    left: 0px;
    bottom: 100px;
    opacity: 0;
    height: 3px;
    width: 3px;
  }
}

@keyframes spark3 {
  0% {
    opacity: 0;
  }

  30% {
    opacity: 1;
    height: 2px;
